#terminal {
  position: relative;
  padding: 1em;
  color: #fff;
  font-family: 'Ubuntu Mono', monospace; }
  #terminal .machine {
    color: #8ae234; }
  #terminal .path {
    color: #4f68ab; }
  #terminal .command {
    position: absolute;
    width: 6em;
    overflow: hidden;
    white-space: nowrap;
    -webkit-animation: type 1.5s steps(20, end);
            animation: type 1.5s steps(20, end); }
  #terminal .cursor {
    margin-left: 6em;
    background-color: #fff;
    -webkit-animation: flash 1.2s infinite, move 1.5s steps(20, end);
            animation: flash 1.2s infinite, move 1.5s steps(20, end); }
    #terminal .cursor:after {
      content: '_'; }
  #terminal .profile {
    visibility: hidden;
    margin-top: 3em;
    -webkit-animation: 1.7s show;
            animation: 1.7s show;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards; }
    #terminal .profile .header {
      color: inherit;
      font-family: inherit; }
    #terminal .profile p {
      font-size: 1.2em;
      text-indent: 2em; }
      #terminal .profile p:before {
        content: '';
        position: absolute;
        left: 0;
        width: 100%;
        height: 2px;
        background: -webkit-gradient(linear, left top, right top, from(#e65c00), to(#f9d423));
        background: -webkit-linear-gradient(left, #e65c00, #f9d423);
        background: -o-linear-gradient(left, #e65c00, #f9d423);
        background: linear-gradient(to right, #e65c00, #f9d423);
        visibility: hidden;
        -webkit-transform: scaleX(0);
            -ms-transform: scaleX(0);
                transform: scaleX(0);
        -webkit-transition: all 0.3s ease-in-out 0s;
        -o-transition: all 0.3s ease-in-out 0s;
        transition: all 0.3s ease-in-out 0s; }
      #terminal .profile p:hover:before {
        visibility: visible;
        -webkit-transform: scaleX(1);
            -ms-transform: scaleX(1);
                transform: scaleX(1); }
    #terminal .profile span {
      display: inline-block;
      font-size: 1.2em;
      text-indent: 2em; }
    #terminal .profile a {
      color: #fff; }
      #terminal .profile a:hover {
        text-decoration: underline; }

@-webkit-keyframes type {
  from {
    width: 0; } }

@keyframes type {
  from {
    width: 0; } }

@-webkit-keyframes flash {
  0% {
    opacity: 0; }
  20% {
    opacity: 1; }
  40% {
    opacity: 1; }
  60% {
    opacity: 1; }
  80% {
    opacity: 0; }
  100% {
    opacity: 0; } }

@keyframes flash {
  0% {
    opacity: 0; }
  20% {
    opacity: 1; }
  40% {
    opacity: 1; }
  60% {
    opacity: 1; }
  80% {
    opacity: 0; }
  100% {
    opacity: 0; } }

@-webkit-keyframes move {
  from {
    margin-left: 0; } }

@keyframes move {
  from {
    margin-left: 0; } }

@-webkit-keyframes show {
  99% {
    visibility: hidden; }
  100% {
    visibility: visible; } }

@keyframes show {
  99% {
    visibility: hidden; }
  100% {
    visibility: visible; } }



